import { NavLink } from "remix";

export const Navbar = () => {
  const toggle = (event) => {
    const target = event.target;

    const sidenav = document.getElementById("nav-list");

    if (target.classList.contains("nav_list")) {
      console.log("works!");
      console.log(target);
    }
  };

  return (
    <header>
      <nav>
        <div className="nav_top">
          <button onClick={toggle}>
            <i className="fa-solid fa-bars"></i>
          </button>
        </div>
        <ul id="nav-list" className="nav_list">
          <li className="nav_list_item">
            <NavLink to="/">
              <i className="fa-solid fa-house"></i> Home
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/characters">
              <i className="fa-solid fa-person"></i> Characters
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/locations">
              <i className="fa-solid fa-location-dot"></i> Locations
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/story">
              <i className="fa-solid fa-pen"></i> Story
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/about">
              <i className="fa-solid fa-address-card"></i> About
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/contact">
              <i className="fa-solid fa-address-book"></i> Contact
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};
